.footer{
    background-image: url("../../assets/unnamed.png");
    background-position: center;
    background-size: cover;
    color: #FFF;
    .title-footer{
        h3{
            color: #FFF;
            text-align: center;
            text-transform: uppercase;
            font-size: 1.5rem;
            font-weight: 500;
        }
        .line{
            border: 1px solid #FBFBFB;
            width: 50%;
        }
    }
    .span-title{
        color: #000;
        font-size: 1.1rem;
        font-weight: 500;
    }
    .input-footer{
        padding: 8px 12px;
        color: #807878;
        border-radius: 15px ;
        outline: none;
        border: none;
        width: 100%;
        font-size: 0.9rem;
    }
    table{
        tr td{
            padding-bottom: 10px;
            text-align: start;
        }
    }
    .div-icon{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1.2rem;
        border-radius: 50%;
        background-color: white;
        height: 15px;
        width: 15px;
    }
    .col-md-3{
        border-left: 2px solid white;
    }
}